<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline>
        <el-form-item prop="code" class="fl">
          <el-input v-model.trim="query.code" clearable placeholder="订单号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="nameOrCode" class="fl">
          <el-input v-model.trim="query.nameOrCode" clearable placeholder="供应商名称或编号" class="filter-item" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="startDate" class="fl">
          <el-date-picker
            v-model="query.startDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="开始时间"
            @change="query.endDate = ''"
          />
        </el-form-item>
        <el-form-item prop="endDate" class="fl">
          <el-date-picker
            v-model="query.endDate"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startDate).getTime()}"
          />
        </el-form-item>
        <!-- <el-form-item prop="recipientName">
          <div>
            <el-input
              v-model.trim="query.recipientName"
              disabled
              placeholder="接收人"
              style="width: 143px;"
            />
            <el-button
              plain
              type="primary"
              style="margin-left:5px;padding-left:0;padding-right:0;width:30px"
              icon="el-icon-search"
              @click="userDialogVisible = true"
            />
          </div>
        </el-form-item> -->
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="searchInit"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
        <el-button
          class="filter-item"
          type="primary"
          icon="el-icon-plus"
          @click="handleAdd"
        >新增寄售定单</el-button>
      </el-form>
    </div>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :refresh-key="refreshKey"
        :operation-width="450"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleViewDeliveryOrder(scope.row)">显示交接单</el-button>
          <el-button type="primary" @click="handleAddDeliveryOrder(scope.row)">新增交接单</el-button>
          <el-button type="primary" @click="handleOrderSelect(scope.row)">选择订单</el-button>
          <el-button type="warning" @click="handleModify(scope.row)">修改</el-button>
          <el-button type="danger" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    <!-- 人员选择 -->
    <origin-user-tree
      id="null"
      ref="addUserDialog"
      v-model="userDialogVisible"
      axios-module="sys"
      get-user-url="/sys/user/listByOffice"
      :multiple="false"
      :multiple-detail="false"
      @submitUserList="submitUserList"
    />
    <!-- 新增 -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :title="dialogTitle"
      @submit="init"
    />
    <!-- 合同下的订单选择 -->
    <order-select v-model="visible" :multiple="false" :extra-info="{contractId: curItem.contractId}" @confirm="handleOrderSelectConfirm" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { originUserChooseMixins } from '@/mixins/tableColumnChoose'
import EditDialog from './Dialog'
import OrderSelect from './OrderSelect'
import { getListApi, deleteListApi, bindOrderApi } from '@/apis/modules/in-storage/consign'
import tableHeadSet from '@/mixins/tableHeadSet'

export default {
  name: 'InStorageConsign',
  components: {
    EditDialog,
    OrderSelect
  },
  mixins: [paginationPage, tableHeadSet, originUserChooseMixins],
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        code: '',
        nameOrCode: '',
        startDate: '',
        endDate: ''
        // recipientId: '', // 接收人id
        // recipientName: '' // 接收人
      },
      tableData: [],
      columnList: [],
      columnSets: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      userDialogVisible: false,
      visible: false,
      curItem: {}
    }
  },
  computed: {
    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      console.log('init method')
      this.loading = true
      const params = {
        ...this.query,
        type: '3',
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // 选择订单
    handleOrderSelect(item) {
      this.visible = true
      this.curItem = item
    },
    // 订单选择确认
    handleOrderSelectConfirm(list = []) {
      if (!list.length) return
      this.bindOrder({
        id: this.curItem.id,
        emptyOrderId: list[0].id
      })
    },
    // 绑定订单
    bindOrder(query = {}) {
      const params = {
        ...query
      }
      return new Promise((resolve, reject) => {
        bindOrderApi({
          ...params
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '订单绑定成功'
            })
            this.init()
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    // 批量删除
    handleDelete(item) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        deleteListApi({
          id: item.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$notify.success({
              title: '成功',
              message: '删除成功'
            })
            this.init()
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    // 修改
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    // 显示交接单
    handleViewDeliveryOrder(item) {
      this.$router.push({
        name: 'InStorageConsignDelivery',
        params: {
          id: item.id,
          type: '3' // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
        }
      })
    },
    // 新增交接单
    handleAddDeliveryOrder(item) {
      this.$router.push({
        name: 'InStorageConsignDeliveryAdd',
        params: {
          view: 'add',
          orderId: item.id, // 物资寄售的订单id
          type: '3', // 订单类型，1：采购订单，2：外协订单，3：寄售订单，4：成品订单
          routeName: this.$route.name
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    // 选择人员
    submitUserList(userInfo) {
      const { id, nickName } = userInfo
      this.query.recipientId = id
      this.query.recipientName = nickName
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
