<template>
  <el-dialog
    :title="title"
    top="5vh"
    :visible="value"
    :show-close="true"
    width="900px"
    :lock-scroll="false"
    :destroy-on-close="true"
    :append-to-body="true"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-scrollbar class="scroll-dialog">
      <!-- 头部搜索 -->
      <div class="clearfix top-filter">
        <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
          <el-form-item label="合同单号" prop="contractCode">
            <el-input v-model.trim="query.contractCode" placeholder="请输入合同编号" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-form-item label="合同名称" prop="contractName">
            <el-input v-model.trim="query.contractName" placeholder="请输入合同名称" style="width: 178px;" class="filter-item" />
          </el-form-item>
          <el-button
            class="filter-item"
            type="success"
            icon="el-icon-search"
            @click="init"
          >搜索</el-button>
          <el-button
            class="filter-item"
            type="warning"
            icon="el-icon-refresh-left"
            @click="resetForm('filterItem')"
          >重置</el-button>
        </el-form>
      </div>
      <!-- 列表 -->
      <el-empty v-if="!columnList.length" :loading="loading" :image-size="850" description="暂无配置" />
      <el-row>
        <table-com
          :ref="refName"
          index
          :table-height-type="480"
          :loading="loading"
          :index-fixed="false"
          :selection="true"
          :multiple="multiple"
          :selection-fixed="false"
          :table-column-sort="false"
          :table-data="tableData"
          :column-list="columnList"
          :show-operate="false"
          :operate-fixed="false"
          :refresh-key="refreshKey"
          :expand-list="expandList"
          :table-selection="tableSelection"
          @refresh="searchInit"
          @selectionChange="selectionChange"
        />
      </el-row>
      <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />
    </el-scrollbar>
    <div slot="footer">
      <el-button type="primary" @click="confirmDialog">确 定</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
import QueryItemMixin from '@/mixins/queryItem'
import paginationPage from '@/mixins/paginationPage'
import tableHeadSet from '@/mixins/tableHeadSet'
import { getContractListApi } from '@/apis/modules/in-storage/consign'

export default {
  name: 'SupplierSelect',
  mixins: [QueryItemMixin, tableHeadSet, paginationPage],
  props: {
    value: {
      required: true,
      type: Boolean
    },
    title: {
      type: String,
      default: '合同选择'
    },
    // 是否多选，如果多选，返回id的数组， 如果单选返回 {id, name}
    multiple: {
      type: Boolean,
      default: true
    },
    // 默认勾选的ids
    selection: {
      type: Array,
      default: () => []
    },
    // 多选状态下的最大选择数量
    max: {
      type: Number,
      default: 0
    },
    // 接口传入的额外信息
    extraInfo: {
      type: Object,
      default: () => {}
    }
  },
  dicts: [],
  data() {
    return {
      loading: false,
      refName: 'modelSelectSupplierSelect',
      query: {
        contractCode: '',
        contractName: ''
      },
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'code',
          label: '合同单号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'name',
          label: '合同名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'supplierName',
          label: '供应商',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'signDate',
          label: '签订日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'shouldDeliveryDate',
          label: '应交货日期',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'currency',
          label: '币种',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'exchangeRate',
          label: '汇率',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      tableSelection: []
    }
  },
  methods: {
    confirmDialog() {
      this.$emit('confirm', this.tableSelection)
      this.beforeClose()
    },
    init() {
      const params = {
        ...this.query,
        page: {
          ...this.pageInfo
        },
        ...this.extraInfo
      }
      this.loading = true
      return new Promise((resolve, reject) => {
        getContractListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list
            // 初始化selection
            this.tableSelection = this.tableData.filter(i => this.selection.includes(i.id))
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    initSelect() {
      this.init()
    },
    selectionChange(tableSelection) {
      this.tableSelection = [...tableSelection]
    },
    // 更新列表复选框勾选
    handleUpdateTableSelected(type = '') {

    },
    handleDefault() {
      this.tableSelection = new Array(0)
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.handleDefault()
      typeof done === 'function' && done()
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>
.scroll-dialog {
  height: 600px;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
 ::v-deep .el-dialog__body {
  padding-top: 10px;
  padding-bottom: 0;
}
</style>
