<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item v-if="editType !== 'add'" label="单号" prop="code">
          <el-input v-model.trim="form.code" :disabled="editType !== 'add'" placeholder="系统自动生成" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="合同" prop="contractId">
          <template>
            <div class="selected-wrapper">
              <el-input v-model.trim="form.contractCode" disabled placeholder="请选择合同" style="width: 143px;" />
              <el-button type="primary" style="margin-left:5px;padding-left:0;padding-right:0;width:30px" icon="el-icon-search" @click="visible = true" />
            </div>
          </template>
        </el-form-item>
        <el-form-item v-if="form.contractId" label="供应商" prop="supplierId">
          <el-input v-model.trim="form.supplierName" disabled placeholder="请填写供应商" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="联系人" prop="contact">
          <el-input v-model.trim="form.contact" placeholder="请填写联系人" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="电话" prop="phone">
          <el-input v-model.trim="form.phone" placeholder="请填写电话" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="日期" prop="purchaseDate">
          <el-date-picker
            v-model="form.purchaseDate"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="请选择日期"
            :clearable="false"
          />
        </el-form-item>

      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>

    <contract-select v-model="visible" :multiple="false" :extra-info="{type: '3'}" @confirm="handleContractSelect" />

  </el-drawer>
</template>

<script>
import ContractSelect from './ContractSelect'
import { addListApi as addApi, updateListApi as updateApi } from '@/apis/modules/in-storage/consign'

const INIT_FORM = () => ({
  code: '', // 订单号
  contractCode: '', // 合同编号
  contractId: '', // 合同id
  contractName: '', // 合同名称
  supplierId: '', // 供应商id
  supplierName: '', // 供应商名称
  contact: '', // 联系人
  phone: '', // 电话
  purchaseDate: '' // 日期
})

export default {
  name: 'InStorageConsignCom',
  components: {
    ContractSelect
  },
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        contractId: [
          { required: true, message: '请选择合同', trigger: 'change' }
        ],
        supplierId: [
          { required: true, message: '请选择供应商', trigger: 'blur' }
        ],
        purchaseDate: [
          { required: true, message: '请选择日期', trigger: 'change' }
        ]
      },
      visible: false
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    initSelect() {
    },
    // 合同选择确认
    handleContractSelect(list) {
      if (!list.length) return
      this.form.contractId = list[0].id
      this.form.contractCode = list[0].code
      this.form.contractName = list[0].name
      this.form.supplierId = list[0].supplierId
      this.form.supplierName = list[0].supplierName
    },
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          const params = {
            ...this.form,
            type: '3'
          }
          const api = {
            add: addApi,
            modify: updateApi
          }[this.editType || 'add']
          api(params).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `操作成功`,
                type: 'success'
              })
              this.$emit('submit')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    }
  }
}
</script>

  <style lang='scss' scoped>
  .container{
    width: 860px;
    margin: 0 auto;
    .left{
      width: 640px;
    }
    .right{
      margin-left: 20px;
      width: 180px;
    }
  }
  ::v-deep:focus {
    outline: 0;
  }
  .scroll-drawer {
    height: calc(100vh - 150px);
    // overflow: auto;
  }
  ::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  ::v-deep .el-drawer {
    width: 640px;
  }
  .drawer-footer{
    text-align: right;
  }
  </style>

